body {
  margin: 0;
  font-family: "Gill Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  font-size: 1em;
}

input, button, select {
  font-size: 1em;
  padding: 0;
  font-family: "Gill Sans", sans-serif;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}

code {
  font-family: 'Courier New', monospace;
}

a, a:hover, a:visited {
  color: #000;
  text-decoration: none;
}